import React from "react";
import styled from "styled-components";
import { useMarksEntry } from "../../../Context/MarksEntry";
import { useExport } from "../../../Context/Export";
import useAxios from "../../../api/Axios";

const ToolsBox = ({ classInfo }) => {
  const axios = useAxios();
  const marks = useMarksEntry();
  const exportData = useExport();
  const [loading, setLoading] = React.useState(true);
  React.useEffect(() => {
    setLoading(true);
    exportData.handleSetClassInfo(classInfo);
    marks.handleSetClassInfo(classInfo);
    const getData = async () => {
      const url = "/student/all?id=" + classInfo.classId;
      await axios.get(url).then((res) => {
        exportData.handleSetStudent(res.data);
      });
    };
    getData();
    setLoading(false);
  }, [axios, classInfo, exportData, marks]);
  return (
    <React.Fragment>
      {loading ? (
        "Loading..."
      ) : (
        <Tools>
          <Button
            onClick={() => exportData.handleOpen(true)}
            style={{ marginRight: "auto" }}
          >
            Export
          </Button>

          <Input
            type="search"
            placeholder="Search"
            defaultValue={marks.search}
            onChange={marks.handleSearch}
          />
        </Tools>
      )}
    </React.Fragment>
  );
};

export default ToolsBox;

const Tools = styled.div`
  display: flex;
  margin: 0.5rem 0;
  padding: 0.3rem 0.5rem;
  width: 100%;
`;

const Input = styled.input`
  padding: 0.3rem 1rem;
  border-radius: 5px;
  border: 1px solid grba(3, 3, 3, 0.5);
`;

const Button = styled.button`
  min-width: 100px;
  font-weight: 500;
  background: green;
  margin: 0 0.3rem;
  color: #fff;
  &:nth-child(1) {
    margin-left: 0;
  }
`;
